.jp-ctrl {
    width: 300px;
    height: 750px;
    border: 1px solid gray;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow: hidden;
}

.jp-ctrl__header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-line-pack: center;
        align-content: center;
}

.jp-ctrl__config {
    position: relative;
    top: -760px;
    width: 300px;
    -ms-flex-preferred-size: 0px;
        flex-basis: 0px;
    height: 0px;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.jp-ctrl__config--show {
    top: 0;
    -ms-flex: 1 0 760px;
        flex: 1 0 760px;
}

.jp-ctrl__config-option {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.jp-ctrl hr {
    width: 100%;
}

.jp-ctrl,
.jp-ctrl label {
    padding: 10px;
}



