.button {
    border: none;
    position: absolute;
    display: -ms-inline-flexbox;
    display: inline-flex;
    cursor: pointer;
    width: 80px;
    height: 50px;
    margin: 5px;
    background: #262626;
    text-transform: uppercase;
    -ms-flex-pack: center;
        justify-content: center;
    line-height: 50px;
    color: #ffee10;
    font-size: 20px;
}
.button::before,
.button::after,
.button span::before,
.button span::after {
    content: '';
    position: absolute;
    width: 10px;
    height: 10px;
    background: #ffee10;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    mix-blend-mode: hue;
}
.button span::before {
    top: -2px;
    left: -2px;
}
.button span::after {
    top: -2px;
    right: -2px;
}
.button::before {
    bottom: -2px;
    left: -2px;
}
.button::after {
    bottom: -2px;
    right: -2px;
}
.button:hover::before,
.button:hover::after,
.button:hover span::before,
.button:hover span::after {
    width: calc( 80px / 2);
    height: calc( 50px / 2);
}



